"use client";
import { Card } from "@/components/ui/card";

const bills = [
  {
    id: 1,
    date: "May 15",
    title: "Figma - Monthly",
    amount: 150,
    sub: "Last Charge - 14 May, 2022",
    logo: "F",
  },
  {
    id: 2,
    date: "Jun 16",
    title: "Adobe - Yearly",
    amount: 559,
    sub: "Last Charge - 17 Jun, 2023",
    logo: "A",
  },
];

export default function UpcomingBills() {
  return (
    <div className="w-full flex justify-center items-start mt-8 px-4">
      <div className="w-full max-w-4xl">
        {/* Heading */}
        <div className="flex justify-between items-center mb-5 flex-wrap">
          <h2 className="text-lg sm:text-xl font-semibold text-gray-700">
            Upcoming Bills
          </h2>
          <button className="text-sm text-teal-600 font-medium hover:underline">
            View All
          </button>
        </div>

        {/* Card */}
        <Card className="p-4 rounded-xl shadow bg-white w-full">
          {/* Desktop Table */}
          <div className="hidden md:block overflow-x-auto">
            <table className="w-full text-sm">
              <thead>
                <tr className="text-gray-500 border-b">
                  <th className="py-2 text-left">Due Date</th>
                  <th className="py-2 text-left">Logo</th>
                  <th className="py-2 text-left">Item Description</th>
                  <th className="py-2 text-left">Last Charge</th>
                  <th className="py-2 text-right">Amount</th>
                </tr>
              </thead>
              <tbody>
                {bills.map((bill) => (
                  <tr key={bill.id} className="border-b last:border-0">
                    <td className="py-3">{bill.date}</td>

                    {/* Logo */}
                    <td className="py-3">
                      <div className="w-9 h-9 flex items-center justify-center rounded-full bg-gray-100 text-teal-600 font-semibold">
                        {bill.logo}
                      </div>
                    </td>

                    {/* Title */}
                    <td className="py-3 font-medium text-gray-800">
                      {bill.title}
                    </td>

                    {/* Last Charge */}
                    <td className="py-3 text-gray-500 text-sm">{bill.sub}</td>

                    {/* Amount */}
                    <td className="py-3 font-semibold text-gray-900 text-right">
                      ${bill.amount}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {/* Mobile Cards */}
          <div className="md:hidden space-y-3">
            {bills.map((bill) => (
              <div
                key={bill.id}
                className="p-3 rounded-lg border border-gray-200 flex items-start gap-3"
              >
                {/* Logo */}
                <div className="w-10 h-10 flex items-center justify-center rounded-full bg-gray-100 text-teal-600 font-semibold shrink-0">
                  {bill.logo}
                </div>

                {/* Info */}
                <div className="flex-1">
                  <div className="flex justify-between">
                    <p className="font-medium text-gray-800">{bill.title}</p>
                    <p className="font-semibold text-gray-900">
                      ${bill.amount}
                    </p>
                  </div>
                  <p className="text-xs text-gray-500">Due: {bill.date}</p>
                  <p className="text-xs text-gray-400">{bill.sub}</p>
                </div>
              </div>
            ))}
          </div>
        </Card>
      </div>
    </div>
  );
}
